<script>
  export let current_page = undefined
  export let last_page = undefined
  export const per_page = undefined
  export const from = undefined
  export const to = undefined
  export const total = undefined
  import { createEventDispatcher } from 'svelte'
  const dispatch = createEventDispatcher()
  function range(size, startAt = 0) {
    return [...Array(size).keys()].map(i => i + startAt)
  }
  function changePage(page) {
    if (page !== current_page) {
      dispatch('change', page)
    }
  }
</script>

<style lang="scss">.pagination {
  display: flex;
  justify-content: center;
  margin-top: 10px; }
  .pagination ul {
    display: flex;
    padding-left: 0;
    list-style: none; }
    .pagination ul li a {
      position: relative;
      display: block;
      padding: 0.5rem 0.75rem;
      margin-left: -1px;
      line-height: 1.25;
      background-color: #ffffff;
      border: 1px solid #f4e6ff;
      text-decoration: none; }
      .pagination ul li a:hover {
        color: #9100ff;
        background-color: #f4e6ff;
        border-color: #f4e6ff;
        text-decoration: none; }
    .pagination ul li.active a {
      color: #ffffff;
      background-color: #9100ff;
      border-color: #9100ff;
      text-decoration: none; }
    .pagination ul li a:hover:not(li.active) {
      color: #9100ff;
      background-color: #f4e6ff;
      border-color: #f4e6ff;
      text-decoration: none; }
    .pagination ul li.disabled a {
      color: #6600b3;
      pointer-events: none;
      cursor: auto;
      border-color: #f4e6ff; }
</style>

<!-- <div class="text-center">
  <p>
    Page {current_page} / {last_page}
    <br />
    Results {from + 1} - {to}
    <br />
    Total
    <strong>{total}</strong>
    users
  </p>
</div> -->

<nav class="pagination">
  <ul>
    <li class={current_page === 1 ? 'disabled' : ''}>
      <a
        href="javascript:void(0)"
        on:click={() => changePage(current_page - 1)}>
        <span aria-hidden="true">«</span>
      </a>
    </li>
    {#each range(last_page, 1) as page}
      <li class={page === current_page ? 'active' : ''}>
        <a href="javascript:void(0)" on:click={() => changePage(page)}>
          {page}
        </a>
      </li>
    {/each}
    <li class={current_page === last_page ? 'disabled' : ''}>
      <a
        href="javascript:void(0)"
        on:click={() => changePage(current_page + 1)}>
        <span aria-hidden="true">»</span>
      </a>
    </li>
  </ul>
</nav>
